.contour-test {
    flex: 1;
    width: 100%;
    height: 100%;
    //background-color: blac;
    background-color: #111111;
    .color-dot {
        position: absolute;
        left: 500px;
        width: 20px;
        background-color: red;
        z-index: 10;
        height: 20px;
        cursor: pointer;
    }
    .color-pickers {
        position: absolute;
        left: 500px;
        top: 100px;
    }
    .mouse-info {
        position: absolute;
        top: 30px;
                      
    }
    .sliders-contours {
        display: flex;
        position: absolute;
        left: 0px;
        right: 0px;
    }
}
