.contour-test {
  -ms-flex: 1 1;
      flex: 1 1;
  width: 100%;
  height: 100%;
  background-color: #111111;
}
.contour-test .color-dot {
  position: absolute;
  left: 500px;
  width: 20px;
  background-color: red;
  z-index: 10;
  height: 20px;
  cursor: pointer;
}
.contour-test .color-pickers {
  position: absolute;
  left: 500px;
  top: 100px;
}
.contour-test .mouse-info {
  position: absolute;
  top: 30px;
}
.contour-test .sliders-contours {
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  left: 0px;
  right: 0px;
}
